.inputForm
{
    background-color: #1b11a1;
    box-shadow:-1px 6px 46px 0px rgba(195, 213, 193, 0.75);
    margin-top:50px;
    padding:20px;
    border-radius:5px;

}
.inputForm input{
    width:300px;
    border:1px solid white;
    padding:10px 10px;
    outline:none;
    margin-bottom:20px;
    font-size:16px;
}
.inputForm textarea
{
    width: 300px;
    border:1px solid white;
    padding:10px 10px;
    outline:none;
    margin-bottom:20px;
    font-size: 16px;
}
.inputForm button
{
    background-color:#961583;
    color:white;
    border:none;
    padding:5px 25px;
    font-size:14px;
    font-weight:bold;
}